@import url(https://fonts.googleapis.com/css?family=Montserrat:600&display=swap);
@import url(https://fonts.googleapis.com/css?family=Questrial&display=swap);
* {font-family: 'Montserrat', sans-serif;}

body {
  margin: 0;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

/* Styles */

/* Login */

.container-master{
    width: 100%;
    height: 100vh;
    display: flex;
    overflow: hidden;
    padding: 0 5rem;
    align-items: center;
    justify-content: flex-end;
    position: relative;
}
.container-login{
    width: 50%;
}
.img-computador{
    width: 27rem;
    pointer-events: none;
}
.login-modal{
    background-color: #0006;
    border-radius: 2rem;
    color: #fff;
    font-family: 'Questrial', sans-serif;
    display: flex;
    justify-content: center;
    flex-direction: column;
    align-items: center;
    margin: 0 6rem;
    padding: 1rem 0;
    -webkit-transform: scale(0.95);
            transform: scale(0.95);
}
.text-copyright{
    margin: 0;
    display: flex;
    align-items: center;
}
.container-copyright{
    display: flex;
    padding-top: 1rem;
    padding-bottom: 1rem;
}
.woombat-logo{
    width: 5rem;
    margin-left: 0.375rem;
    pointer-events: none;
}
.frontend-logo{
    width: 8rem;
    pointer-events: none;
}
.frontend-logo-container{
    padding-top: 1rem;
    padding-bottom: 0.5rem;
}
.title-input{
    margin: 0;
    text-transform: uppercase;
}
.input-login{
    width: 16rem !important;
    margin-top: 0.5rem !important;
}
.input-container{
    padding-bottom: 1rem;
    padding-top: 0.5rem;
}
.button-login{
    width: 16rem;
}
.ajax-logo{
    width: 11rem;
}
.ajax-container{
    padding: 1rem 0;
}
.background{
    width: 100%;
    height: 100vh;
    position: absolute;
    top: 0%;
    z-index: -1;
    left: 0%;
    -webkit-transform: translateY(-15%);
            transform: translateY(-15%);
}
/* Sweet Alerts Styles */

.test{
    display: flex;
    flex-direction: column;
}

/* General Settings */

.swal2-container:not(.swal2-top):not(.swal2-top-start):not(.swal2-top-end):not(.swal2-top-left):not(.swal2-top-right):not(.swal2-center-start):not(.swal2-center-end):not(.swal2-center-left):not(.swal2-center-right):not(.swal2-bottom):not(.swal2-bottom-start):not(.swal2-bottom-end):not(.swal2-bottom-left):not(.swal2-bottom-right):not(.swal2-grow-fullscreen)>.swal2-modal{
    display: flex;
    position: absolute;
    background-color: #444;
    box-shadow: 0px 0px 12px 0px;
}
.swal2-container.swal2-shown{
    position: absolute;
    overflow-y: auto;
    width: 84%;
    z-index: 1;
    display: flex;
    left: 16em;
    justify-content: center;
    align-items: center;
}

.swal2-popup{
    border-radius: 1rem;
}
.swal2-styled.swal2-confirm{
    background-color: #0fb2a6;
    border-radius: 10rem;
    -webkit-transition: 0.3s;
    transition: 0.3s;
}

.swal2-actions:not(.swal2-loading) .swal2-styled:hover{
    background-color: #0d988e;
}

.swal2-styled:focus{
    outline: 0;
    box-shadow: 0 0 0 2px #0fb2a6, 0 0 0 4px rgba(15, 178, 166, 0.4);
}

.content-master{
    background-color: #444;
    box-shadow: 0 1px 12px #111;

}
.swal2-header, .swal2-content, .swal2-actions, .swal2-footer{
    -webkit-transform: scale(0.8) !important;
            transform: scale(0.8) !important;
}

.swal2-content{
    color: #fff;
}
/* Error Alert */

.title-error{
    color: #d73333;
    text-transform: uppercase;
}
.swal2-icon.swal2-error{
    border-color: #da3333;
}
.swal2-icon.swal2-error [class^=swal2-x-mark-line]{
    background-color: #da3333;
}

/* Question Alert */

.question-wizard{
    font-size: 1rem;
}
.swal2-icon.swal2-question{
    border-color: #8de4e2;
    color: #8de4e2;
}
.title-question{
    color: #8de4e2;
}
.button-cancel{
    background-color: #b12424 !important;
    border-radius: 10rem !important;
    -webkit-transition: 0.3s !important;
    transition: 0.3s !important;
}
.button-cancel:hover{
    background-color: #8b1b1b !important;
}
.button-cancel:focus{
    outline: 0;
    box-shadow: 0 0 0 2px #711414, 0 0 0 4px rgb(82, 17, 17);
}

/* Login Alert Successfully */

.swal2-popup{
    background-color: #444;
}
.swal2-title{
    color: #fff;
}
.swal2-popup.swal2-toast{
    box-shadow: 0px 0px 12px 0px;
}
/* Styles */



/* Landing */

.container-landing-master{
    width: 100%;

    grid-row: 1/2;
}
.container-header-landing{
    background-color: #020633;
    padding: 0rem 2rem;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    height: 100%;
}
.text-header-landing{
    color: #fff;
    font-family: 'Questrial', sans-serif;
    margin: 0;
    font-size: 0.9rem;
}
.icon-container-landing{
    font-size: 1.5rem;
    padding-right: 2rem;
}
.icon-landing{
    color: #fff;
}
.text-header-landing-two{
    margin: 0;
    color: #fff;
    cursor: pointer;
}
.bar-separator-landing{
    width: 1px;
    height: 2rem;
    background-color: #fff;
    margin: 0 1rem;
}
.container-body-landing{
    width: 100%;
    height: 100%;
    display: flex;
    grid-row: 2/13;
    display: flex;
    justify-content: center;
}
.container-landing-left-menu{
    width: 20%;
    height: 100vh;
    display: flex;
    background-color: #062C59;
    align-items: center;
    padding: 3rem 0;
    flex-direction: column;
    z-index: 5;
}
.container-main-landing{
    width: 100%;
    height: 100vh;
    display: grid;
    grid-template-rows: repeat(12,1fr);
    overflow: hidden;
    position: relative;
}
.user-default-left-menu{
    width: 4rem;
    pointer-events: none;
}
.text-left-menu{
    margin: 0;
    color: #fff;
    font-family: 'Questrial', sans-serif;
    line-height: 1.5em;
    
}
.text-container-left-menu{
    padding: 0.5rem 0;
    text-align: center;
    width: 100%;
}
.text-container-left-menu-singled {
    padding: 0.5rem 0;
    text-align: center;
    width: 140%;
    margin-left: -2.2em;
}
.span-left-menu{
    width: 110%;
    background-color: #27FCF6;
    position: absolute;
    left: 0%;
    padding: 0.4rem 0;
    display: flex;
    text-transform: uppercase;
    color: #062c59;
    justify-content: space-around;
    cursor: pointer;
}
.span-left-menu-singled {
    width: 110%;
    background-color: #27FCF6;
    left: 0%;
    padding: 0.4rem 0;
    display: flex;
    text-transform: uppercase;
    color: #062c59;
    justify-content: space-around;
    cursor: pointer;
}
.icon-span-plus{
    display: flex !important;
    align-items: center;
}
.container-span-left-menu{
    padding: 2rem 0;
    position: relative;
}
.container-span-left-menu-singled {
    padding: 0;
    position: relative;
}
.mark-left-menu{
    background-color: #778ba2;
    width: 92%;
    height: 280px;
    position: absolute;
    right: -32%;
    margin-top: -7em;
    z-index: -1;
    -webkit-clip-path: polygon(64% 15%, 64% 23%, 76% 23%);
    clip-path: polygon(64% 15%, 64% 23%, 76% 23%);
}
.text-menu-left{
    color: #9ec7c6;
    margin: 0;
    font-family: 'Questrial', sans-serif;
    font-size: 1rem;
}
.container-master-text-menu-left{
    padding: 1.2rem 2rem;
    flex-direction: column;
    align-items: flex-start;
}
.container-text-menu-left{
    line-height: 3em;
    display: flex;
    align-items: baseline;
    justify-content: space-between;
    cursor: pointer;
    width: 100%;
}
.text-close-session{
    margin: 0;
    color: #fff;
    font-family: 'Questrial', sans-serif;
    text-transform: uppercase;
    width: 90%;
    display: flex;
    justify-content: space-around;
    align-items: center;
}
.container-text-close-session{
    background-color: #831193;
    width: 90%;
    height: auto;
    padding: .6em;
    border-radius: 0.5rem;
    font-size: 0.8rem;
    cursor: pointer;
    -webkit-transition: 0.3s;
    transition: 0.3s;
    
}   
.container-text-close-session:hover{
    background-color: #630c6e;
}
.container-texts-signout{
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    height: 60vh;
}
.icon-title-menu-left{
    color: #9ec7c6 !important;
}
.container-body-empty{
    display: flex;
    align-items: center;
    flex-direction: column;
    justify-content: center;
}
.body-empty-text{
    color: #000;
    margin: 0;
    padding: 1rem 0;
}
.img-empty-body{
    width: 14rem;
    pointer-events: none;
}
.text-buttom-create-proyect{
    color: #fff;
    margin: 0;
    text-transform: uppercase;
}
.buttom-create-proyect{
    background-color: #020633;
    padding: 0.3rem 3rem;
    border-radius: 0.7rem;
    cursor: pointer;
    -webkit-transition: 0.3s;
    transition: 0.3s;
}
.buttom-create-proyect:hover{
    background-color: #01031a;
}

.text-buttom-proyectos{
    margin: 0;
    color: #fff;
    background-color: #00b9b4;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    text-transform: uppercase;
    padding: 0.7rem 1.5rem;
    border-radius: 0.6rem;
    font-family: 'Questrial', sans-serif;
    font-size: 1rem;
}
.buttom-proyectos{
    padding-bottom: 1rem;
}
.title-proyect-input{
    margin: 0;
    text-transform: uppercase;
    color: #020633;
    padding-bottom: 0.5rem;
    font-size: .9em;
    /* display: grid;
    grid-template-columns: repeat(3, 1fr);
    grid-template-rows: 1fr; */
}

.container-master-descripcion-up{
    width: 100%;
    display: flex;
}
.container-title-descripcion{
    width: 50%;
}
.container-date-input{
    width: 50%;
}
.container-title-descripcion:first-child{
    margin-right: 2rem;
}
