    border-radius: 0.6rem !important;
    height: 1.5rem !important;
    border: 1px solid #062c59 !important;
    color: #020633;
} */
.container-master-descripcion{
    padding-top: 1rem;
}
.container-master-proyectos{
   display: grid;
   grid-template-columns: repeat(12, 1fr);
   grid-template-rows: repeat(12, 1fr);
    width: 100%;
    padding: 2rem 5rem;
    grid-gap: 2em;
} /*               Contenedor Padre                     */

.container-left{
    width: 100%;
    grid-column: 1/7;
    grid-row: 1/7;
}
.container-right{
    width: 100%;
    grid-column: 7/13;
    grid-row: 1/7;
}


.container-final-process{
    width: 100%;
    grid-row: 12/13;
    grid-column: 12/13;
}

.text-upload-file{
    margin: 0;
}
.text-buttom-final{
    color: #fff;
    margin: 0;
    text-transform: uppercase;
    display: flex;
    align-items: center;
}
.buttom-final{
    padding: 0.7rem 2rem;
    background-color: #0FB2A6;
    border-radius: 0.7rem;
    cursor: pointer;
    -webkit-transition: 0.3s;
    transition: 0.3s;
}
.buttom-final:hover{
    background-color: #0e968d;
}
.icon-buttom-final{
    padding-right: 0.3rem;
}
.text-area-resize{
    resize: none;
}
.container-checkbox-technologies{
    background-color: #fff;
    width: -webkit-fit-content;
    width: -moz-fit-content;
    width: fit-content;
    border: 0.094rem solid #062C59;
    border-radius: 0.6875rem;
    padding: 0.625rem 1rem;
    cursor: pointer;
    margin-left: 0.5rem;
    display: flex;
    align-items: center;
}
.checkbox-active{
    background-color: #e2e2e2;
}
.brands-checkbox{
    width: 2rem;
    pointer-events: none;
}
.container-technologies{
    display: flex;
    justify-content: center;
}
.text-technologies{
    margin: 0;
}



.container-upload-files{
    width: 100%;
    height: 100%;
    grid-column: 1/13;
    grid-row: 8/11  ;
}
.container-upload-files:hover {
    cursor: pointer;
}

.upload-icon {
    width: 100%;
    justify-self: center;
    font-size: 6em;
}

/*  FilePond Overwrite  */
.filepond--drop-label  {
    color: rgba(0, 0, 0, .8) !important;
    width: 100% !important;
    border: .5px dashed gray !important;
    border-radius: 5px !important;
}   
.filepond--list {
    display: grid !important;
    grid-template-columns: repeat(3, 1fr) !important;
    
}
.filepond--item {
    width: 32.5% !important;
    
}
.filepond--file-wrapper {
    display: grid !important;
    
    grid-template-columns: repeat(3, 1fr) !important;
}

.filepond--drop-label label:hover{
    cursor: pointer;
}

.pikachu-container {
    -webkit-animation: show-pika 1500ms;
            animation: show-pika 1500ms;
    -webkit-transform: scale(.3);
            transform: scale(.3);
    position: absolute;
    right: -10%;
    top: 50%;
    z-index: 1000000;
    -webkit-transition: ease-in-out 600ms;
    transition: ease-in-out 600ms;
}

@-webkit-keyframes show-pika {
    25% { top: -50% }
}

@keyframes show-pika {
    25% { top: -50% }
}
